import React, {useState} from 'react';
import Tweet from './Tweet';

const Tweets = () => {

        const [tweets, setTweets] = useState([
            {
                id: 1,
                name: "Dave Rad",
                email: "daverad@hotmail.com", 
                handle: "@dave",
                tweet: "this is my tweet",
                phone: "204-555-1212",
                likes: 0
            },    
            { 
                id: 2,
                name: "Josef Brosef", 
                email: "josefbrosef@hotmail.com", 
                handle: "@josefbrosef",
                tweet: "this is my tweet",
                phone: "204-555-1210",
                likes: 0
            },   
            {
                id: 3,
                name: "Willamina Schmidt", 
                email: "Willamina@hotmail.com", 
                handle: "@willamina",
                tweet: "this is my tweet",
                phone: "555-555-1211",
                likes: 0
            },
            { 
                id: 4,
                name: "Poppi Lemiux", 
                email: "PoppiLemiux@hotmail.com", 
                handle: "@poppilemiux",
                tweet: "this is my tweet",            
                phone: "555-555-1214",
                likes: 0
            }
         
        ]);

        const handleUpVote = id => {
            const newState = [...tweets];
            const index = tweets.findIndex(el => el.id === id);
            newState[index].likes += 1;
            setTweets(newState);
        };

        const handleDownVote = id => {
            const newState = [...tweets]; 
            const index = tweets.findIndex(el => el.id === id); 
            if (newState[index].likes > 0) {
                newState[index].likes -= 1;
                setTweets(newState);
            }

        };

    
        const renderTweets = () => {
            return tweets.map(el => {
                return (
                <Tweet 
                handleDownVote = {handleDownVote}
                handleUpVote = {handleUpVote} tweet={el}
                />
                );
            });
        };

    return (
        <div>
            {renderTweets()}
        </div>
    );
};

export default Tweets;